import React, {useState} from "react";
import {Input, List, Modal} from "antd";
import {EditTwoTone} from "@ant-design/icons";
import {actionPermissions, dataPermissionsGroups, dataPermissionsPupils} from "../getting-switchs";
import {handleOk} from "../server-request-role";

const ModalRoleEdit = ({Roles, setRoles, setSkeletonShow, name_role, role}) => {

    const [visible, setVisible] = useState(false)

    let actionPermissionsForm = {},
        dataPermissionsForm = {
            forPupil: {
                phone: undefined,
                parentPhone: undefined,
                parentNSM: undefined,
                balance: undefined,
                discord: undefined,
                paymentHistory: undefined,
                notes: undefined,
                localSchedule: undefined,
                groups: undefined,
                name: 1,
                surname: 1,
                midname: 1,
                statuses: undefined
            },
            forGroup: {
                groupsHistory: undefined,
                tutors: undefined,
                group_name: undefined,
                level: undefined,
                tutor: undefined,
                pupils: undefined,
                places: undefined,
                global_schedule: undefined
            }
        },
        nameRole = name_role

    return (
        <>
            <EditTwoTone
                style={{
                    fontSize: '20px',
                    position: 'absolute',
                    top: '19px',
                    right: '55px'
                }}
                onClick={() => {
                    setVisible(true)
                }}
            />
            <Modal
                width={1000}
                title={`–†–µ–¥–∞–∫—Ç–∏—Ä–æ–≤–∞—Ç—å —Ä–æ–ª—å: ${name_role}`}
                visible={visible}
                onCancel={() => {
                    setVisible(false)
                }}
                onOk={() => {
                    handleOk(
                        actionPermissionsForm,
                        nameRole,
                        dataPermissionsForm,
                        setVisible,
                        () => {
                        },
                        Roles,
                        (res) => {
                            let index = 0
                            Roles.forEach((item, i) => {
                                if (item._id === role._id) {
                                    index = i
                                }
                            })
                            let box = [...Roles]
                            box.splice(index, 1, res)
                            setRoles(box)
                        },
                        setSkeletonShow,
                        '—Å–æ—Ö—Ä–∞–Ω–µ–Ω–∞',
                        'PATCH',
                        role._id
                    )
                }}
            >
                <List

                    size="small"
                    bordered={false}
                >
                    <List.Item>
                        –ù–∞–∑–≤–∞–Ω–∏–µ —Ä–æ–ª–∏:
                        <Input
                            onChange={(e) => {
                                nameRole = e.target.value
                            }}
                            defaultValue={name_role}/>
                    </List.Item>
                    <div style={{display: 'flex'}}>
                        <div className="boxLeft col-6" style={{position: 'relative'}}>
                            <List.Item style={{fontSize: "16px", fontWeight: "500"}}>
                                –†–∞–∑—Ä–µ—à—ë–Ω–Ω—ã–µ –¥–µ–π—Å—Ç–≤–∏—è:
                            </List.Item>
                            {actionPermissions(role, actionPermissionsForm)}
                        </div>
                        <div className="boxRights col-6">
                            <List.Item style={{fontSize: "16px", fontWeight: "500"}}>
                                –ú–æ–∂–Ω–æ –≤–∏–¥–µ—Ç—å:
                            </List.Item>
                            {dataPermissionsPupils(role, dataPermissionsForm)}
                            {dataPermissionsGroups(role, dataPermissionsForm)}
                        </div>
                    </div>
                </List>
            </Modal>
        </>
    )
}

export default ModalRoleEdit